/* MEDIA */

img {

	&[align="left"] {
		margin-top: 20px;
		margin-bottom: 20px;
		margin-right: 20px;
	}

	&[align="right"] {
		margin-top: 20px;
		margin-bottom: 20px;
		margin-left: 20px;
	}
}

/* ----------------------------------------------- */

.entry-content img.wp-smiley,
.comment-content img.wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

.wp-caption {
	border: 1px solid $gray-10;
	margin: 10px;
	max-width: 100%;
	padding-top: 4px;
	text-align: center;
	background-color: $gray-2;

	img[class*="wp-image-"] {
		display: block;
		margin: 1.2% auto 0;
		max-width: 98%;
	}

	.wp-caption-text {
		text-align: center;
		margin: 0.8075em 0;
	}
}

.site-content {

	.gallery {
		margin-bottom: 1.5em;

		a img {
			border: none;
			height: auto;
			max-width: 90%;
		}

		dd {
			margin: 0;
		}
	}
}

/* Make sure embeds and iframes fit their containers */
embed,
iframe,
object {
	max-width: 100%;
}
